import React, {useEffect, useState} from "react";
import {NavLink} from "react-router-dom";

let People = (props) => {
    console.log( props.people)
    let [current,SetCurrent]=useState(props.current);

    useEffect(()=>{
        debugger
        SetCurrent(props.current)
    },[props.current])

    debugger
    let fu=(type)=>{
        debugger
        let d=current
        type==='--' && d--;
        type==='++' && d++;
        props.itsRename('LIST',d)

    }
    return (
        <div className='List-People'>

            <div className={"LIST_PAGE"}>
                <span onClick={()=>fu('--')} >{'<'}</span>

                <span>{current} </span>
                <span onClick={()=>fu('++')} >{'>'}</span>
            </div>


            <div >
                {
                    props.people.length===0
                    ?()=>props.itsRename('LIST',current--)

                    :props.people.map(z =>(
                        <div key={z._id}>
                            <div className='People-Item'>
                                    {z.friendStatus

                                            ? <div>
                                                <img className="its-sub"
                                                    onClick={() => {
                                                    props.itsRename('SUB',z._id)
                                                }} src={z.Description.PhotoMin.origin!==''?z.Description.PhotoMin.origin:z.Description.PhotoMin.default} alt="Pic"/>
                                            </div>
                                            : <div>
                                                <img className="not-sub"
                                                    onClick={() => {
                                                    props.itsRename('SUB',z._id)
                                                }} src={z.Description.PhotoMin.origin!==''?z.Description.PhotoMin.origin:z.Description.PhotoMin.default} alt="Pic"/>
                                            </div>
                                    }
                                    <div className='People-Name'>
                                        <div>
                                            <div><NavLink to={'/profile/' + z._id} className={'Name-to'}>{z.Description.Nickname.map(e=>e).join(' ')}</NavLink></div>
                                            <div><NavLink to={'/profile/' + z._id} className={'Name-to'}>Я скажу так:"{z.Description.Status}"</NavLink></div>
                                            <div id={'row'}>{z.Description.Tags.map(z=><div> #{z}</div>)}</div>
                                        </div>
                                        <div>
                                            <div>{z.Description.Location.Count}</div>
                                            <div>{z.Description.Location.City}</div>
                                        </div>
                                    </div>
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    )
}
export default People;