import React, {useCallback, useEffect, useState} from 'react';
import '../../style/App.css';
import {connect} from 'react-redux';
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";
import People from "./People";
import {FollowPeople, GetUser, SetCurrent} from "./PeopleReducer";
import {Loading} from "../Login/LoginReducer";
import {getCurrent, getLoadingPeople, getPeople} from "../../user-selector/PeopleSelector";
import PeopleMenu from "./PeopleMenu";
import LoAd from "../../conponent/load";

let  PeopleCont=(props)=> {

    let [listTags, SetListTags]=useState('');
    let [nickname, SetNickname]=useState('');

    let [current, SetCurrent]=useState(1);

  useEffect(()=>{

      props.GetUser({
          tags: listTags.match(/(\w+)/gim) || []
          , nickname: nickname.match(/(\w+)/gim) || []
          , list: current
      });
  },[current]);

    let itsRename=(name,value)=> {
        switch (name) {
            case "NICKNAME" :
                return  SetNickname(value);
            case "TAGS" :
                return SetListTags(value);
            case "DOUBLE":
                return  props.GetUser({
                    tags: listTags.match(/(\w+)/gim) || []
                    , nickname: nickname.match(/(\w+)/gim) || []
                    , list: current
                });
            case "LIST":
                return SetCurrent(value);
            case "SUB":
                return props.FollowPeople(value);
            default :
                return 'Not Methods'
        }
    }

        return <div>
                      <div className={"People"}>
                          {props.loading && <div className={'Loading'}> <LoAd/> </div>}

                          <div >
                              <PeopleMenu itsRename={itsRename} listTags={listTags} nickname={nickname}/>
                          </div>
                          <div>
                              <People people={props.people} itsRename={itsRename} current={current}/>
                          </div>
                       </div>
               </div>
};

let mapState = (state) => {
    return {
        people: getPeople(state)

        ,loading: getLoadingPeople(state)
        ,current: getCurrent(state)
    }
};
export default compose(
    connect(mapState, {SetCurrent,GetUser, Loading, FollowPeople})
    , withAuthRedirect
)(PeopleCont)




















